.container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

@media (max-width:459px){
  .japa {
  display: none;
}
}




.title {
  text-align: center;
}
table {
  width: 100%;
}
th {
  text-align: left;
  font-size: x-large;
}
input {
  padding: 0.3rem;
  border: 1px solid green;
}
input:focus{
  background-color: rgb(192, 29, 165);
}
button {
  padding: 7px;
  border-radius: 10%;
  border: 1px solid darkolivegreen;
}
button:hover{
  background-color: darkolivegreen;
}